
.background-home {
    /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#4c4c4c+49,4c4c4c+50,f9f9f9+50,f9f9f9+50 */
    background: #2C3034; /* Old browsers */
    background: -moz-linear-gradient(left,  #2C3034 49%, #2C3034 50%, #f9f9f9 50%, #f9f9f9 50%); /* FF3.6-15 */
    background: -webkit-linear-gradient(left,  #2C3034 49%,#2C3034 50%,#f9f9f9 50%,#f9f9f9 50%); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to right,  #2C3034 49%,#2C3034 50%,#f9f9f9 50%,#f9f9f9 50%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#4c4c4c', endColorstr='#f9f9f9',GradientType=1 ); /* IE6-9 */
    width: 100%;
    height: 100vh;
    
}

.center-box {
    background: #2C3034;
    margin: auto;
    width: 30%;
    height: fit-content;
    border: solid 3px #03A9F4;
    display: flex;
    flex-direction: column;
    padding: 3rem 5rem;

}

.submit-button {
  width: 80%;
  align-self: center;
}

.google-auth {
  width: 90%;
  text-align: center;
}
  
button {
  background: #03A9F4;
  padding: 10px 16px;
  width: auto;
  font-weight: 600;
  text-transform:  uppercase;
  font-size: 14px;
  color: #fff;
  line-height: 16px;
  letter-spacing: 0.5px;
  border-radius: 2px;
  box-shadow: 0 2px 6px rgba(0,0,0,0.1), 0 3px 6px rgba(0,0,0,0.1);
  border: 0;
  outline: 0;
  margin: 15px 20px 15px 20px;
  transition: all 0.25s;
}

.home-button:hover {
  background: #0288D1;
  box-shadow: 0 4px 7px rgba(0,0,0,0.1), 0 3px 6px rgba(0,0,0,0.1);
}

